<template>
	
	<div>
		
		<div class="modal fade" id="modalAdd" tabindex="-1" role="dialog" aria-labelledby="modalStatus" aria-hidden="true"  data-backdrop="static" data-keyboard="false">
			<div class="modal-dialog modal-lg" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLongTitle">Kewajiban Pelaporan</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">
						<div class="row">
							<div class="col-md-5">
								<div class="form-group" v-if="!is_single">
									<label>Nama Ijin</label>
									<input type="text" disabled="" class="form-control" :value="document ? document.labeled_name : '-'">
								</div>
								<div class="form-group">
									<label>Jenis Pelaporan</label>
									<v-select v-model="model.reporting.reportTypeId" :options="reportTypes" :reduce="type => type.value" class="form-control" @input="getReminderItem"></v-select>
								</div>
								<div class="form-group">
									<label>Instansi Tujuan</label>
									<v-select :options="instances" :reduce="instance => instance.value" class="form-control" v-model="model.reporting.instanceId"></v-select>
								</div>
								<div class="form-group">
									<label>Pengingat Laporan</label>
									<table class="table table-bordered">
										<thead>
											<tr>
												<th>Waktu</th>
												<th>Aktif?</th>
											</tr>
										</thead>
										<tbody>
											<tr v-if="reminderItems.length == 0">
												<td colspan="2" class="text-center">Belum Ada Data</td>
											</tr>
											<tr v-else v-for="(v,i) in reminderItems" :key="i">
												<td>{{ v }}</td>
												<td>
													<input v-if="reportingSchedules.includes(v)" type="checkbox" @click="toggleReminder(v, $event)" checked="" class="report-check">
													<input v-else type="checkbox" @click="toggleReminder(v, $event)" class="report-check">
												</td>
											</tr>
										</tbody>
									</table>
								</div>
							</div>
							<div class="col-md-7">
								<div class="form-group">
									<label>Nama Laporan</label>
									<v-select v-model="model.reporting.reportingNameId" :options="reportingNames" :reduce="type => type.value" class="form-control"></v-select>
								</div>
								<div class="form-group">
									<label>Dokumen Pelaporan</label>
									<input type="file" class="form-control" @change="onFileChange" id="input-file">
								</div>
								<div class="form-group">
									<label>Tanggal Pelaporan</label>
									<input type="date" class="form-control" v-model="model.firstReportingDate">
								</div>
								<div class="form-group">
									<label>Penanggung Jawab</label>
									<table class="table table-bordered">
										<thead style="display: table; width: 100%; table-layout: fixed;">
											<tr>
												<th width="50%">Penanggung Jawab</th>
												<th width="50%">Ingatkan?</th>
											</tr>
										</thead>
										<tbody style="max-height: 250px; overflow-y: auto;display: block">
											<tr v-if="pics.length == 0">
												<td colspan="2" class="text-center">Belum Ada Data</td>
											</tr>
											<tr v-else v-for="(v,i) in pics" :key="i" style="display: table; width: 100%; table-layout: fixed;">
												<td width="51%">{{ v.label }}</td>
												<td width="49%">
													<input v-if="selectedPic.includes(v.value)" type="checkbox" @click="togglePic(v.value, $event)" class="pic-check" checked="">
													<input v-else type="checkbox" @click="togglePic(v.value, $event)" class="pic-check">
												</td>
											</tr>
										</tbody>
									</table>
								</div>
							</div>
							<div class="col-md-12 mt-2">
								<div class="form-group">
									<label>Keterangan Pelaporan</label>
									<textarea class="form-control" rows="3" v-model="model.reporting.description"></textarea>
								</div>
							</div>
						</div>
					</div>
					<div class="modal-footer">
						<button class="btn btn-danger" @click="clearCheck" data-dismiss="modal">Batal</button>
						<button @click="saveReport" class="btn btn-success">Simpan</button>
					</div>
				</div>
			</div>
		</div>

	</div>

</template>

<script type="text/javascript">

	import utils from '@/mixins/util'
	import alert from '@/mixins/alert'
	import { reportTypeAll } from '@/api/reportType'
	import { reportingSave, reportingUpdate, reportingShow } from '@/api/reporting'

	export default{
		name: 'Report',
		mixins: [
		utils,
		alert,
		],
		props: [
		'instances',
		'pics',
		'id',
		'document',
		'getReport',
		'reportingNames',
		],
		data(){
			return {
				is_single: false,
				model: {
					reporting: {
						id: null,
						documentId: null,
						reportingNameId: null,
						reportingTimeTableId: null,
						instanceId: null,
						reportTypeId: null,
						description: null,
					},
					piciDs: [],
					reportingSchedules: [],
					reportFile: null,
					firstReportingDate: null,
				},
				reportingSchedules: [],
				reminderItems: [],
				reportTypes: [
				{
					value: 1,
					label: 'Bulanan'
				},
				{
					value: 2,
					label: 'Triwulan'
				},
				{
					value: 3,
					label: 'Semester'
				},
				{
					value: 4,
					label: 'Tahunan'
				},
				],
				selectedReminder: [],
				selectedPic: [],
			}
		},
		methods: {
			async getReportTypes(){
				try{
					let request = await reportTypeAll();
					let data = request.data;

					data.forEach((v) => {
						this.reportTypes.push({
							value: v.id,
							label: v.name
						})
					})
				}catch(e){
					console.log(e);
				}
			},
			getReminderItem(v){
				this.reminderItems = [];

				var reminderItems = [
				'1 Tahun',
				'6 Bulan',
				'3 Bulan',
				'2 Bulan',
				'1 Bulan',
				'2 Minggu',
				'1 Minggu',
				'3 Hari',
				];

				if(v == 1){
					reminderItems = this.removeItems([
						'1 Tahun',
						'6 Bulan',
						'3 Bulan',
						'2 Bulan',
						], reminderItems);
				}
				if(v == 2){
					reminderItems = this.removeItems([
						'1 Tahun',
						'6 Bulan',
						], reminderItems);
				}
				if(v == 3){
					reminderItems = this.removeItems([
						'1 Tahun',
						], reminderItems);
				}

				this.reminderItems = reminderItems;
			},
			async saveReport(){

				if(this.model.reporting.id != null){
					this.updateReport();
					return;
				}else{
					this.model.reporting.documentId = this.id;
					if(this.validateForm()){
						this.showLoading();
						try{
							let data = this.model;
							let schedules = [];
							data.reportingSchedules.forEach((v) => {
								schedules.push({
									name: v,
								})
							})

							let formData = new FormData();
							formData.append("json", JSON.stringify({
								reporting: this.model.reporting,
								reportingSchedules: schedules,
								piciDs: this.model.piciDs,
								firstReportingDate: this.model.firstReportingDate
							}));
							formData.append("reportFile", this.model.reportFile);

							let request = await reportingSave(formData);
							this.hideLoading();
							$("#modalAdd").modal("hide");
							this.formClear();
							this.getReport();
						}catch(e){
							this.hideLoading();
							this.showNotif("Gagal Menambahkan Pelaporan");
							console.log(e);
						}
					}else{
						this.showNotif("Semua Field Harus Diisi");
					}
				}
			},
			async updateReport(){
				console.log("updating report");
				console.log("with pics", this.model.piciDs);
				if(this.validateForm(true)){
					this.showLoading();
					try{
						let data = this.model;
						let schedules = [];
						data.reportingSchedules.forEach((v) => {
							schedules.push({
								name: v,
							})
						})

						let formData = new FormData();
						formData.append("json", JSON.stringify({
							reporting: this.model.reporting,
							reportingSchedules: schedules,
							piciDs: this.model.piciDs,
							firstReportingDate: this.model.firstReportingDate
						}));
						formData.append("reportFile", this.model.reportFile);

						let request = await reportingUpdate(formData);
						this.hideLoading();
						$("#modalAdd").modal("hide");
						this.formClear();
						this.getReport();
					}catch(e){
						this.hideLoading();
						this.showNotif("Gagal Mengedit Pelaporan");
						console.log(e);
					}
				}else{
					this.showNotif("Semua Field Harus Diisi");
				}
			},
			validateForm(edit = false){
				let data = this.model;
				let excludes = ['id', 'documentId', 'reportingTimeTableId'];
				let valid = true;
				console.log(edit);

				for(var k in data.reporting){
					if(!excludes.includes(k) && (data.reporting[k] == "" || data.reporting[k] == null)){
						console.log(k);
						valid = false;
					}
				}
				if(!edit){
					if(data.firstReportingDate == null || data.firstReportingDate == ""){
						valid = false;
					}
					if(data.reportingSchedules.length == 0){
						valid = false;
					}
					if(data.piciDs.length == 0){
						valid = false;
					}
					if(data.reportFile == null){
						valid = false;
					}
				}

				return valid;
			},
			toggleReminder(name, event = null){
				let checked = true;
				if(event){
					checked = $(event.target).is(":checked");
				}

				if(checked){
					this.model.reportingSchedules.push(name);
				}else{
					this.model.reportingSchedules = this.removeItems([name], this.model.reportingSchedules);
				}
			},
			togglePic(name, event = null){
				let checked = true;
				if(event){
					checked = $(event.target).is(":checked");
					console.log("is checked", checked);
				}

				if(checked){
					console.log("adding pic");
					console.log(name);
					this.model.piciDs.push(name);
				}else{
					console.log("removing pic");
					console.log(name);
					this.model.piciDs = this.removeItems([name], this.model.piciDs);
				}
			},
			onFileChange(e) {
				var files = e.target.files || e.dataTransfer.files;
				if (!files.length)
					return;
				this.model.reportFile = files[0];
			},
			showAdd(){
				this.model =  {
					reporting: {
						id: null,
						documentId: null,
						reportingNameId: null,
						reportingTimeTableId: null,
						instanceId: null,
						reportTypeId: null,
						description: null,
					},
					piciDs: [],
					reportingSchedules: [],
					reportFile: null,
					firstReportingDate: null,
				};
				let pics = this.pics;
				this.pics = [];
				this.pics = pics;
				this.selectedPic = [];
				this.reminderItems = [];
			},
			async showEdit(data){
				let pics = this.pics;
				$("#modalAdd").modal("show");
				this.showLoading();
				let schedules = [];
				this.reportingSchedules = [];

				try{
					this.pics = [];
					let request = await reportingShow(data?.id);
					let firstReportingDate = request.data?.firstReportingDate?.split("T")?.[0];
					request.data?.reportingSchedules.forEach((v) => {
						schedules.push(v.name);
					})
					this.getReminderItem(request.data?.reporting?.reportTypeId);

					let remItems = this.reminderItems;
					this.reminderItems = [];
					this.reminderItems = remItems;

					console.log("reminder items", this.reminderItems);
					this.pics = pics;
					this.model = {
						firstReportingDate: firstReportingDate,
						reporting: request.data?.reporting,
						piciDs: request.data?.piciDs,
						reportingSchedules: schedules,
						reportFile: null,
					};
					this.reportingSchedules = schedules;
					console.log("reporting schedules are", schedules);
					this.selectedPic = request.data?.piciDs;
					console.log("selected pics", this.selectedPic);
					this.hideLoading();
				}catch(e){
					console.log(e);
					this.hideLoading();
				}
			},
			formClear(){
				this.model = {
					reporting: {
						documentId: null,
						reportingNameId: null,
						reportingTimeTableId: null,
						instanceId: null,
						reportTypeId: null,
						description: null,
					},
					piciDs: [],
					reportingSchedules: [],
					reportFile: null,
					firstReportingDate: null,
				};
				this.reminderItems = [];
				$(".report-check").prop('checked', false);
				$(".pic-check").prop('checked', false);
				$("#input-file").val("");
			},
			clearCheck(){
				this.reportingSchedules = [];
				this.selectedPic = [];
				this.formClear();
			}
		}
	}
</script>