import { httpClient,  httpClientForm } from './httpClient';

const END_POINT = '/api/reporting_name';

const reportNameAll = () => {
	return httpClient.get(END_POINT + "/");
}
const reportNameById = (id) => {
	return httpClient.get(END_POINT + "/" + id);
}
const reportNameAdd = (data) => {
	return httpClient.post(END_POINT + "/", data);
}
const reportNameUpdate = (id, data) => {
	return httpClient.put(END_POINT + "/" + id, data);
}
const reportNameDelete = (id) => {
	return httpClient.delete(END_POINT + "/" + id);
}

export {
	reportNameAll,
	reportNameById,
	reportNameAdd,
	reportNameUpdate,
	reportNameDelete,
}