<template>
	
	<div>
		<div class="modal fade" id="modalDetail" tabindex="-1" role="dialog" aria-labelledby="modalStatus" aria-hidden="true">
			<div class="modal-dialog modal-lg" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title" id="exampleModalLongTitle">Kewajiban Pelaporan</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">
						<div class="text-right mb-2">
							<a :href="formatUrl" target="_blank" class="btn btn-primary">Download Form Pelaporan</a>
						</div>
						<table class="table table-bordered">
							<thead>
								<tr>
									<th>Tanggal Wajib Lapor</th>
									<th>Keterangan Laporan</th>
									<th>Form Terlapor</th>
									<th>Tanda Terima</th>
								</tr>
							</thead>
							<tbody>
								<tr v-if="datas == null || datas.length == 0">
									<td colspan="4" class="text-center">Belum Ada Data</td>
								</tr>
								<tr v-else v-for="(v,i) in datas" :key="i">
									<td>{{ v.time }}</td>
									<td>{{ v.period }}</td>
									<td>
										<div class="text-center" v-if="v.reporting.length == 0">
											<a @click="setUploadState('report', v.id)" class="text-primary"><i class="fas fa-upload"></i> upload laporan</a>
										</div>
										<div v-else>
											<div v-for="(v2,i2) in v.reporting" :key="i2">
												<a :href="v2.link" target="_blank" class="mb-1">
													<i class="fas fa-download"></i> {{ v2.path }}
												</a>
											</div>
											<hr>
											<div class="text-center">
												<a @click="setUploadState('report', v.id)" class="text-primary"><i class="fas fa-upload"></i> upload revisi</a>
											</div>
										</div>
									</td>
									<td>
										<div class="text-center" v-if="v.receipt.length == 0">
											<a @click="setUploadState('receipt', v.id)" class="text-primary"><i class="fas fa-upload"></i> upload tanda terima laporan</a>
										</div>
										<div v-else>
											<div v-for="(v2,i2) in v.receipt" :key="i2">
												<a :href="v2.link" target="_blank" class="mb-1">
													<i class="fas fa-download"></i> {{ v2.path }}
												</a>
											</div>
											<hr>
											<div class="text-center">
												<a @click="setUploadState('receipt', v.id)" class="text-primary"><i class="fas fa-upload"></i> upload tanda terima revisi</a>
											</div>
										</div>
									</td>
								</tr>
							</tbody>
						</table>
						<input type="file" @change="event => doUpload(event)" id="file-input" style="display: none;">
					</div>
					<div class="modal-footer">
						<button @click="onSave" class="btn btn-success">Simpan</button>
					</div>
				</div>
			</div>
		</div>
	</div>

</template>

<script type="text/javascript">

	import util from '@/mixins/util'
	import alert from '@/mixins/alert'
	import { reportingDetail, uploadReport, uploadReceipt } from '@/api/reporting'

	export default{
		name: 'Report',
		mixins: [
		util,
		alert,
		],
		data(){
			return{
				currentId: null,
				upload: {
					state: null,
					targetId: null,
				},
				formatUrl: null,
				datas: [],
			}
		},
		methods: {
			async getDetail(id, format){
				this.datas = [];
				this.showLoading();

				try{
					let request = await reportingDetail(id);
					let data = request.data;
					let parsed = [];
					this.formatUrl = format;
					this.currentId = id;

					data.forEach((v,i) => {
						let repFiles = [];
						let recFiles = [];

						v?.reportedFilesResult?.forEach((f) => {
							let file = f?.path;
							repFiles.push({
								path: file,
								link: process.env.VUE_APP_API_URL + "/api/reporting/get_file/name?fileName=" + escape(file)
							})	
						})
						v?.reportingReceiptFilesResult?.forEach((f) => {
							let file = f?.path;
							recFiles.push({
								path: file,
								link: process.env.VUE_APP_API_URL + "/api/reporting/get_file/name?fileName=" + escape(file)
							})
						})

						parsed.push({
							id: v?.reportingTimeTable?.id,
							time: this.formatDate(v?.reportingTimeTable?.time),
							period: v?.reportingTimeTable?.period,
							reporting: repFiles,
							receipt: recFiles
						})
					})
					this.datas = parsed;
					this.hideLoading();
				}catch(e){
					this.hideLoading();
					console.log(e);
					if(e.response?.status == 401){
						this.invalidSession();
					}
				}
			},
			setUploadState(state, targetId){
				this.upload.state = state;
				this.upload.targetId = targetId;

				$("#file-input").click();
			},
			async doUpload(elem){
				let input = $(elem.target);
				let target = elem.target.files[0];
				let formData = new FormData();
				formData.append("reportFile", target);

				if(this.upload.state == 'report'){
					await this.reportUpload(formData);
					$("#file-input").val("");
				}else{
					await this.receiptUpload(formData);
					$("#file-input").val("");
				}
			},
			async reportUpload(data){
				this.showLoading();

				try{
					let request = await uploadReport(this.upload.targetId, data);
					this.hideLoading();
					this.getDetail(this.currentId, this.formatUrl);
				}catch(e){
					console.log(e);
					this.hideLoading();
					this.showNotif("Gagal Mengupload File");
					if(e.response?.status == 401){
						this.invalidSession();
					}
				}
			},
			async receiptUpload(data){
				this.showLoading();

				try{
					let request = await uploadReceipt(this.upload.targetId, data);
					this.hideLoading();
					this.getDetail(this.currentId, this.formatUrl);
				}catch(e){
					console.log(e);
					this.hideLoading();
					this.showNotif("Gagal Mengupload File");
					if(e.response?.status == 401){
						this.invalidSession();
					}
				}
			},
			onSave(){
				this.$toast.open({
					message: 'Kewajiban Pelaporan Berhasil Disimpan !',
					type: 'info',
					position: 'top-right',
				});
				$("#modalDetail").modal("hide");
			},
			async invalidSession(){
				alert("Sesi sudah tidak valid, silahkan login");
				await this.$store.dispatch("auth/authLogout");
				this.$router.replace("/login");
			}
		}
	}
</script>